.dialogs {
    max-width: 480px;
    min-width: 480px;
    background-color: #212121;
    height: 100vh;
    border-right: 1px solid #2d2d2d;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.dialogs__form {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
}

.dialogs__form-container {
    width: 100%;
    transition: 0.5s;
}

.dialogs__form-button {
    margin-left: 10px;
    position: relative;
    width: 20px;
    height: 10px;
    display: flex;
    align-items: center;
    justify-content: center;

    &::before,
    &::after {
        position: absolute;
        content: '';
        width: 20px;
        height: 4px;
        background-color: #FFFFFF;
        border-radius: 10px;
        transition: 0.5s;
    }

    &::before {
        transform: rotate(45deg);
    }

    &::after {
        transform: rotate(-45deg);
    }

    &:hover {

        &::before,
        &::after {
            background-color: #8774e1;
        }

    }
}

.search-user {
    margin-top: 15px;
}

.search-user__text {
    font-size: 16px;
    font-weight: 700;
    margin-bottom: 10px;

    &:last-child {
        margin-top: 10px;
    }
}

.dialogs__list {
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
    overflow-y: scroll;
}

.dialogs__list-info {
    height: calc(100vh - 140px) !important;
}

.dialog {
    display: flex;
    align-items: center;
    margin-top: 15px;
    padding: 10px;
    border-radius: 10px;
    cursor: pointer;
    transition: 0.5s;

    &:hover {
        background-color: #2c2c2c;
    }
}

.dialog--active {
    background-color: #8774e1;

    .dialog__info-date {
        color: #FFFFFF;
    }

    &:hover {
        background-color: #8774e1;
    }
}

.dialog__avatar {
    margin-right: 10px;
    max-width: 54px;
    min-width: 54px;
    height: 54px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;

    img {
        width: 54px;
        height: 54px;
        border-radius: 50%;
    }
}

.dialog__avatar-no {
    border: 2px solid #FFFFFF;

    p {
        font-size: 22px;
        font-weight: 700;
    }
}

.dialog__avatar-img {
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

.dialog__info {
    width: 100%;
}

.dialog__info-top {
    margin-bottom: 10px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.dialog__info-date {
    font-size: 12px;
    color: #686c72;
    transition: 0.5s;
}

.dialog__info-title {
    font-size: 20px;
    font-weight: 700;
}

.dialog__info-last-message {
    font-size: 16px;
    font-weight: 700;
    max-width: 350px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.current-user {
    display: flex;
    align-items: center;
    background-color: #2c2c2c;
    padding: 10px;
    border-radius: 10px;
}

.current-user__avatar {
    width: 54px;
    height: 54px;
    border-radius: 50%;
    margin-right: 15px;
}

.current-user__avatar-no {
    border: 2px solid #FFFFFF;
    display: flex;
    align-items: center;
    justify-content: center;

    p {
        font-size: 22px;
        font-weight: 700;
    }
}

.current-user__avatar-img {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

.current-user__info-full-name {
    font-weight: 700;
    font-size: 20px;
    margin-bottom: 10px;
}

.current-user__info-id {
    font-size: 14px;
}

.dialog__info-last-message-picture {
    display: flex;
    align-items: center;
}
 
.dialog__info-last-message-img {
    width: 20px;
    height: 20px;
    background-size: cover;
    background-position: center center;
    border-radius: 5px;
    margin-right: 10px;
}